* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.weather {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.weather-content-now {
  width: 100%;
  height: 100%;
  text-align: center;
  color: white;
  font-size: 18px;
  font-weight: 600;
}
.title-now {
  font-size: 38px;
}
.weather-week {
  width: 100%;
  display: flex;
  color: white;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 50px;
  padding-left: 10px;
  padding-right: 10px;
}
.weather-week-title {
  width: 100%;
  text-align: center;
  font-size: 20px;
  color: white;
}
.weather-details {
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-size: 18px;
  font-weight: 600;
}

#root {
  display: flex;
  background-image: url(https://i.pinimg.com/originals/39/62/7b/39627ba970f978b8756c7c1d2f591f62.jpg);
  background-size: cover;
  color: white;
  min-height: 100vh !important;
  justify-content: center;
}
.App {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  min-height: 100vh !important;
  margin: 0px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}
.wrapper {
  display: flex;
  height: 100vh;
  height: auto;
}
.bigContainer {
  display: flex;
  flex-direction: column;
  height: auto;
  margin-left: 10px;
  margin-right: 10px;
}

.coordinate-form {
  margin-bottom: 60px;
  position: relative;
  width: 100%;
  margin: 0 auto;
}

.coordinate-input {
  color: white;
  font-size: 20px;
  background: transparent;
  box-sizing: border-box;
  border: 1px solid white;
  height: 35px;
  padding: 15px;
  padding-right: 40px;
  outline: none;
  width: 100%;
}
.coordinate-input::placeholder {
  color: rgba(48, 99, 144, 0.6);
  font-size: 15px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.weather-left-form {
  display: flex;
  flex-direction: column;
  gap: 40px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.weather-right-form {
  display: flex;
  flex-direction: column;
  font-size: 30px;
}

.weather-big-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.weather-header-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  justify-content: center;
  text-align: center;
  gap: 40px;
  margin-right: 50px;
}
.nameCity {
  font-size: 35px;
  color: rgba(8, 81, 145);
}
.date {
  font-size: 25px;
  color: rgba(8, 81, 145);
}

.yearsContainer {
  position: sticky;
  top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center; /* Центрируем кнопки по горизонтали */
  margin-left: auto;
  padding: 30px 10px; /* Объединили padding-top/bottom/right */
  background-color: rgba(144, 48, 114, 0.5);
  width: 20%;
  min-width: 100px;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  scroll-snap-stop: always;
  gap: 70px; /* Добавим промежуток между кнопками */
}

@media (max-width: 604px) {
  .yearsContainer {
    display: none;
  }
  .the_biggest_container,
  .veryBigContainer {
    height: 100vh;
    max-width: 100%;
  }
  .veryBigContainer {
    margin: 0;
  }
  .coordinate-form,
  .calendar {
    width: 95% !important;
  }
}

.error-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.error-popup-content {
  background-color: #f8d7da;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #f5c6cb;
  max-width: 400px;
  width: 100%;
  position: relative;
  color: darkslategrey;
}

.close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 20px;
  cursor: pointer;
}
